<template>
   <div class="activity-smaller-card card-box">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       活跃统计
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;color: #ff7d3d;">
             对客户每月中各天数的活动频次进行统计分析，可分析得出用户的工作情况、作息规律、交易频次。【不包含自动收支、自动退款流水】
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-opera-group">
        <change-tab-group
          :tab-data="['日活跃','月活跃']"
          primary
          @change="onTabChange"
        />
     </div>
     <div class="card-content">
       <div class="chart-container" style="width:100%; height:300px"></div>
     </div>
   </div>
</template>

<script>
import ChangeTabGroup from "@/components/ChangeTabGroup";
import * as echarts from "echarts";

let chartElement = null;
export default {
  name: "ActivitySmallerDimensionCard",
  components: {ChangeTabGroup},
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      chart: null,
      activeTab: 0, // 默认日活跃
    }
  },
  watch: {
    dimensionData(val) {
      console.log(val,"1111")
      this.createChart(val)
    }
  },
  created() {
  },
  mounted() {
    this.createChart(this.dimensionData)
   
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose()
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize()
    },
    createChart(obj) {
      if (chartElement) {
        chartElement.dispose();
        chartElement = null;
      }
      chartElement = echarts.init(document.querySelector(".activity-smaller-card .chart-container"));

      if (this.activeTab === 0) {
        if (!obj || !obj.hour || !obj.hour.labelList) return;
        const data = obj.hour;
        chartElement.setOption({
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          legend: {
            data: ["微信", "支付宝"],
            icon: 'circle',
            right: '5%',
            top: '5%',
            orient: 'horizontal',
            textStyle: {
              fontSize: 8   // 字体大小
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [{ type: 'category', boundaryGap: false, data: data.labelList }],
          yAxis: [{ type: 'value' }],
          series: [
            {
              name: '微信',
              type: 'line',
              smooth: true,
              stack: 'Total',
              emphasis: {
                focus: 'series'
              },
              data: data.dataList1,
              areaStyle: {
                color: '#52a8f7',
                opacity: '0.1',
              }
            },
            {
              name: '支付宝',
              type: 'line',
              smooth: true,
              stack: 'Total',
              data: data.dataList2,
              areaStyle: {
                color: '#28c445',
                opacity: '0.1',
              }
            },
          ]
        });
      } else if (this.activeTab === 1) {
        if (!obj || !obj.chart_calendar_alipay || !obj.chart_calendar_wechat) return;
        const labelList = this.getCurrentMonthLabels();
        const dataList1 = this.getCurrentMonthData(obj.chart_calendar_wechat.data, labelList);
        const dataList2 = this.getCurrentMonthData(obj.chart_calendar_alipay.data, labelList);
        chartElement.setOption({
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          legend: {
            data: ["微信", "支付宝"],
            icon: 'circle',
            right: '5%',
            top: '5%',
            orient: 'horizontal',
            textStyle: {
              fontSize: 8   // 字体大小
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [{ type: 'category', boundaryGap: false, data: labelList.map(i => i.show) }],
          yAxis: [{ type: 'value' }],
          series: [
            {
              name: '微信',
              type: 'line',
              data: dataList1,
              areaStyle: {
                color: '#52a8f7',
                opacity: '0.1',
              }
            },
            {
              name: '支付宝',
              type: 'line',
              data: dataList2,
              areaStyle: {
                color: '#28c445',
                opacity: '0.1',
              }
            },
          ]
        });
      }
    },
    getCurrentMonthLabels() {
      const now = new Date();
      const year = now.getFullYear();
      const month = now.getMonth() + 1;
      const days = new Date(year, month, 0).getDate();
      const labelList = [];
      for (let i = 1; i <= days; i++) {
        labelList.push({
          show: `${i.toString().padStart(2, '0')}日`,
          value: `${year}-${month.toString().padStart(2, '0')}-${i.toString().padStart(2, '0')}`
        });
      }
      return labelList;
    },
    getCurrentMonthData(dataObj, labelList) {
      return labelList.map(item => dataObj[item.value] || 0);
    },
    onTabChange(tab) {
      this.activeTab = tab;

      this.createChart(this.dimensionData);
    }
  }
}
</script>

<style scoped>

</style>
