<template>
  <div>
    <LoadingPage v-if="showLoading" @loadFinish="showLoading = false" />
    <el-watermark
      content="天穹数据"
      :gap="[400, 400]"
      :font="waterFont"
      v-if="!showLoading"
    >
      <div class="report-data">
        <div class="page-content">
          <div class="header-box">
            <div class="report-name">天穹数据风控报告</div>
            <div class="report-other">
              <!--            <div>报告编号: 【缺失】</div>-->
              <div>生成时间: {{ reportData.reportTime }}</div>
            </div>
            <div class="custom-divider"></div>
            <div class="report-info">
              <div class="report-info__left">
                <div class="user-simple">
                  <div class="name">{{ reportData.personInfo.name }}</div>
                  <div>{{ reportData.personInfo.sexText }}</div>
                  <el-divider direction="vertical" />
                  <div>{{ reportData.personInfo.age }}岁</div>
                  <i
                    class="iconfont icon-id-card"
                    style="margin: 0 5px 0 8px"
                  ></i>
                  <div>{{ reportData.personInfo.idCard }}</div>
                </div>
                <div class="user-address">
                  <div>{{ reportData.personInfo.nativePlace }}</div>
                  <!--                <div class="remark">一线城市【缺失】</div>-->
                  <!--                <div class="aero">-->
                  <!--                  <i class="iconfont icon-warning" style="margin-right: .05208rem;font-size: 11px"/>-->
                  <!--                  "该区域法院不允受理租机类起诉案件 【缺失】"-->
                  <!--                </div>-->
                </div>
                <div class="range-title">账单取用范围</div>
                <div class="range-bot">
                  <template
                    v-if="
                      reportData.billStat &&
                      reportData.billStat.wechatInfo &&
                      reportData.billStat.wechatInfo.num > 0
                    "
                  >
                    <el-tooltip
                      placement="top"
                      popper-class="monitor-yt-popover"
                    >
                      <div class="weixin block">
                        <i class="iconfont icon-weixin"></i>
                        <div class="time">
                          {{ reportData.billStat.wechatInfo.timeLapse }}
                          <span
                            >（{{
                              reportData.billStat.wechatInfo.monthNum
                            }}个月）</span
                          >
                        </div>
                        <div class="num">
                          <div>{{ reportData.billStat.wechatInfo.num }}</div>
                          <div class="unit">份</div>
                        </div>
                      </div>
                      <template v-slot:content>
                        <div class="bot__pop">
                          <span class="account"
                            >账号：{{
                              reportData.billStat.wechatInfo.timeRanges[0]
                                .account
                            }}</span
                          >
                          <span>{{
                            reportData.billStat.wechatInfo.timeRanges[0]
                              .timeRange
                          }}</span>
                        </div>
                      </template>
                    </el-tooltip>
                  </template>
                  <template
                    v-if="
                      reportData.billStat &&
                      reportData.billStat.alipayInfo &&
                      reportData.billStat.alipayInfo.num > 0
                    "
                  >
                    <el-tooltip
                      placement="top"
                      popper-class="monitor-yt-popover"
                    >
                      <div class="zhifubao block">
                        <i class="iconfont icon-zhifubao"></i>
                        <div class="time">
                          {{ reportData.billStat.alipayInfo.timeLapse }}
                          <span
                            >（{{
                              reportData.billStat.alipayInfo.monthNum
                            }}个月）</span
                          >
                        </div>
                        <div class="num" style="background-color: #52a8f7">
                          <div>{{ reportData.billStat.alipayInfo.num }}</div>
                          <div class="unit">份</div>
                        </div>
                      </div>
                      <template v-slot:content>
                        <div class="bot__pop">
                          <span class="account"
                            >账号：{{
                              reportData.billStat.alipayInfo.timeRanges[0]
                                .account
                            }}</span
                          >
                          <span>{{
                            reportData.billStat.alipayInfo.timeRanges[0]
                              .timeRange
                          }}</span>
                        </div>
                      </template>
                    </el-tooltip>
                  </template>
                </div>
              </div>
              <div class="report-info__right">
                <div class="risk-title">
                  <span>综合风险评定</span>
                  <el-tooltip
                    placement="right"
                    popper-class="monitor-yt-popover"
                    :visible-arrow="false"
                  >
                    <i class="iconfont icon-question" />
                    <template v-slot:content>
                      <div
                        class="bot__pop"
                        style="
                          padding: 0;
                          width: 2.60417rem;
                          line-height: 0.125rem;
                          font-size: 0.0625rem;
                        "
                      >
                        综合风险评定是基于账单数据分析所得出的评估参考值，分数越高代表表现越佳，分数越低则意味着潜在风险越大。请注意，此评分仅适用于本报告。
                      </div>
                    </template>
                  </el-tooltip>
                </div>
                <div class="level-box level-box--special">
                  <img
                    src="@/assets/images/no_level.png"
                    alt=""
                    class="level-img"
                  />
                  <div class="level-score" style="color: rgb(215, 224, 232)">
                    <span></span><span></span>
                  </div>
                </div>
                <div class="suggestion">
                  <div>
                    <div class="title">风控建议：</div>
                    <div class="tips">
                      <span>
                        <p style="font-weight: 400">
                          <span style="color: #fd8b46; font-weight: 700">
                            <i
                              class="iconfont icon-warning"
                              style="font-size: 0.08292rem"
                            />
                            该用户支付宝账单时长不满足6个月，无法生成风险评分</span
                          >
                        </p>
                      </span>
                    </div>
                  </div>
                </div>
                <div class="remark">
                  注：当微信/支付宝均上传，且两者账单时间均大于6个月的情况下生成该风险评分。
                </div>
              </div>
            </div>
            <img
              class="header-box-info-bg"
              src="@/assets/images/head-info-right.png"
              alt=""
            />
          </div>
          <div class="user-feature">
            <img
              alt=""
              class="staples staples-left"
              src="@/assets/images/staples.png"
            />
            <img
              src="@/assets/images/staples.png"
              class="staples staples-right"
              alt=""
            />
            <div class="user-feature__content">
              <div class="title">特征画像</div>
              <div class="feature-group">
                <div class="feature-box">
                  <i class="iconfont icon-car" />
                  <div class="type">车类消费</div>
                  <div class="detail">详情</div>
                </div>
                <div class="feature-box">
                  <i class="iconfont icon-residence" />
                  <div class="type">住宅消费</div>
                  <div class="detail">详情</div>
                </div>
                <div class="feature-box feature-box--active">
                  <i class="iconfont icon-credit-card" />
                  <div class="type">信用卡还款</div>
                  <div class="detail" @click.stop="showBillCardDrawer = true">
                    详情
                  </div>
                </div>
                <div class="feature-box">
                  <i class="iconfont icon-investment" />
                  <div class="type">投资理财</div>
                  <div class="detail">详情</div>
                </div>
                <div class="feature-box">
                  <i class="iconfont icon-income" />
                  <div class="type">工资收入</div>
                  <div class="detail">详情</div>
                </div>
                <div class="feature-box">
                  <i class="iconfont icon-social-security" />
                  <div class="type">社保医疗</div>
                  <div class="detail">详情</div>
                </div>
              </div>
            </div>
          </div>
          <div class="block-container" id="duoTou">
            <div class="block-title">
              <div>多头查询</div>
              <div class="take-care">注：不包含本次查询</div>
            </div>
            <div class="block-content">
              <el-table
                :header-cell-style="{
                  textAlign: 'center',
                  backgroundColor: '#F5F7FA'
                }"
              >
                <el-table-column prop="" label="多头查询" />
                <el-table-column prop="days3" label="近3天" />
                <el-table-column prop="days7" label="近7天" />
                <el-table-column prop="days14" label="近14天" />
                <el-table-column prop="days30" label="近30天" />
                <el-table-column prop="days90" label="近90天" />
                <el-table-column prop="days180" label="近180天" />
              </el-table>
            </div>
          </div>
          <div class="block-container" id="riskLevel">
            <div class="block-title">
              <div>风险分析</div>
            </div>
            <div class="block-subtitle">
              <div>账单数据概况及分析结果</div>
              <el-tooltip
                placement="right"
                popper-class="monitor-yt-popover"
                :visible-arrow="false"
              >
                <i class="iconfont icon-question" />
                <template v-slot:content>
                  <div
                    class="bot__pop"
                    style="
                      padding: 0;
                      width: 2.60417rem;
                      line-height: 0.125rem;
                      font-size: 0.0625rem;
                    "
                  >
                    受限于数据样本、范围及分析方法，本服务提供的风险等级及使用建议不能完全反映真实客观情况，仅供参考。
                  </div>
                </template>
              </el-tooltip>
            </div>
            <div class="block-content">
              <el-table
                :span-method="objectSpanMethod"
                :data="riskData"
                :header-cell-style="{
                  textAlign: 'center',
                  backgroundColor: '#F5F7FA'
                }"
              >
                <el-table-column
                  prop="riskLevel"
                  label="数据评级"
                  align="center"
                  width="120px"
                />
                <el-table-column
                  type="index"
                  align="center"
                  label="序号"
                  width="80"
                />
                <el-table-column
                  prop="riskName"
                  label="风险项"
                  align="center"
                  width="250px"
                />
                <el-table-column label="解读建议">
                  <template v-slot="scope">
                    <div
                      class="risk-rol-flex"
                      style="
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                      "
                    >
                      <div v-html="scope.row.riskInterpret"></div>
                      <el-link
                        type="primary"
                        @click.stop="hanndleRiskDetail(scope.row)"
                        >详情</el-link
                      >
                    </div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="block-container" id="sceneRecognition">
            <div class="block-title">
              <div>场景识别</div>
            </div>
            <div class="block-content">
              <scene-overall-transaction-card
                ref="sceneOverallTransactionCard"
                :dimension-data="overallTransactionData"
              />
              <scene-lease-card v-if="lease.length" :lease="lease" @openLocation="handleOpenLocation" />
            </div>
            <div class="block-content block-content-2" style="margin-top: 20px">
              <scene-online-lending-card :lending="lending" @openLocation="handleOpenLocation" />
              <scene-performance-card :performance="performance" @openLocation="handleOpenLocation" />
            </div>
          </div>
          <div class="block-container" id="activeAnalysis">
            <div class="block-title">
              <div>活跃分析</div>
            </div>
            <div class="block-content block-content-2">
              <activity-smaller-dimension-card
                :dimension-data="activeSmallerData"
                ref="activitySmallerDimensionCard"
              />
              <activity-bigger-dimension-card
                :dimension-data="activeBiggerData"
                ref="activityBiggerDimensionCard"
              />
            </div>
          </div>
          <div class="block-container" id="transactionFlowAnalysis">
            <div class="block-title">
              <div>交易流水分析</div>
            </div>
            <div class="block-content">
              <flow-money-monthly-card ref="flowMoneyMonthlyCard" :dimension-data="flowMoneyMonthlyData"/>
            </div>
            <div class="block-content">
              <flow-opposite-party-card :data="flowOppositePartyData" @openLocation="handleOpenLocation"/>
            </div>
            <div class="block-content block-content-2">
              <flow-bank-card-card :dimension-data="flowBankCardData" @card-name-click="handleCardNameClick"/>
              <flow-money-direction-card ref="flowMoneyDirectionCard" :dimension-data="flowMoneyDirectionData"/>
            </div>
          </div>
          <div class="block-container" id="transactionProfiling">
            <div class="block-title">
              <div>交易画像分析</div>
            </div>
            <div class="block-content block-content-2">
              <portrait-transaction-card
                ref="portraitTransactionCard"
                :dimension-data="portraitTransactionData"
              />
              <portrait-label-card
                ref="portraitLabelCard"
                :dimension-data="portraitLabelData"
              />
            </div>
          </div>
          <div class="block-container" id="billContent">
            <div class="block-title">
              <div>查找账单内容</div>
            </div>
            <div class="block-content">
              <bill-card :billData="records" />
            </div>
          </div>
          <div class="footer-box">
            <div class="footer">
              <div class="footer__title">报告使用说明：</div>
              <div class="footer__content">
                1.本报告内容仅通过邮箱收到的账单原件进行分析而成，依据使用单位的委托由我司代理完成。我司仅负责数据分析，不对报告内容准确性、完整性等问题承担任何责任。
              </div>
              <div class="footer__content">
                2.本报告仅供使用者参考，我司不承担据此报告产生的任何法律责任。
              </div>
            </div>
          </div>
        </div>
        <div class="down-report-btn">
          <el-button type="primary" size="small">下载报告</el-button>
        </div>
        <div class="page-anchor">
          <el-anchor @change="pageAnchorChangeHandle">
            <el-anchor-link :href="`#duoTou`">多头</el-anchor-link>
            <el-anchor-link :href="`#riskLevel`">风险</el-anchor-link>
            <el-anchor-link :href="`#sceneRecognition`">场景</el-anchor-link>
            <el-anchor-link :href="`#activeAnalysis`">活跃</el-anchor-link>
            <el-anchor-link :href="`#transactionFlowAnalysis`"
              >流水</el-anchor-link
            >
            <el-anchor-link :href="`#transactionProfiling`"
              >画像</el-anchor-link
            >
            <el-anchor-link :href="`#billContent`">明细</el-anchor-link>
          </el-anchor>
        </div>
      </div>
    </el-watermark>
    <el-drawer
      v-model="showBillCardDrawer"
      :lock-scroll="false"
      :modal="false"
      direction="btt"
      :size="750"
    >
      <template v-slot:header>
        <div class="custom-drawer-header">
          <div>查找账单内容</div>
        </div>
      </template>
      <bill-card :billData="records" :externalSearchKey="externalSearchKey" />
    </el-drawer>
  </div>
</template>

<script>
import axios from 'axios'
import { ElMessage } from 'element-plus'
import ActivitySmallerDimensionCard from '@/components/ActivitySmallerDimensionCard'
import ActivityBiggerDimensionCard from '@/components/ActivityBiggerDimensionCard'
import SceneOverallTransactionCard from '@/components/SceneOverallTransactionCard'
import SceneLeaseCard from '@/components/SceneLeaseCard'
import { debounce } from 'lodash'
import SceneOnlineLendingCard from '@/components/SceneOnlineLendingCard.vue'
import ScenePerformanceCard from '@/components/ScenePerformanceCard.vue'
import PortraitTransactionCard from '@/components/PortraitTransactionCard'
import PortraitLabelCard from '@/components/PortraitLabelCard'
import BillCard from '@/components/BillCard'
import FlowOppositePartyCard from '@/components/FlowOppositePartyCard'
import FlowBankCardCard from '@/components/FlowBankCardCard'
import FlowMoneyDirectionCard from '@/components/FlowMoneyDirectionCard'
import LoadingPage from '@/components/LoadingPage'
import FlowMoneyMonthlyCard from "@/components/flowMoneyMonthlyCard";


let handleResizeFun = null
let bodyClickFun = null
export default {
  name: 'ReportData',
  components: {
    LoadingPage,
    FlowMoneyDirectionCard,
    FlowBankCardCard,
    FlowOppositePartyCard,
    BillCard,
    PortraitLabelCard,
    PortraitTransactionCard,
    ScenePerformanceCard,
    SceneOnlineLendingCard,
    FlowMoneyMonthlyCard,
    SceneLeaseCard, SceneOverallTransactionCard, ActivityBiggerDimensionCard, ActivitySmallerDimensionCard},
  data() {
    return {
      showLoading: true,
      waterFont: { color: 'rgba(0, 0, 0, .08)', fontSize: 25 },
      reportData: {
        personInfo: {}
      },
      riskCodes: [],
      riskData: [], // 风险数据
      spanArr: [],
      colFields: ['riskLevel', 'riskIndex', 'riskItem', 'riskExplain'],
      activeSmallerData: {},
      activeBiggerData: {},
      overallTransactionData: {},
      portraitTransactionData: {},
      portraitLabelData: {},
      flowMoneyDirectionData: {},
      flowOppositePartyData: {
        counterpartTop7: [],
        counterpartTop15: [],
        counterpartTop30: [],
        counterpartTop90: [],
        counterpartTop180: [],
        counterpartTop360: []
      },
      flowBankCardData: {},
      showBillCardDrawer: false,
      flowMoneyMonthlyData: {},
      lease: [],
      lending: [],  // 添加网贷数据数组
      performance: [],  // 添加还款数据数组
      records: [],
      externalSearchKey: '' // 外部搜索关键字
    }
  },
  mounted() {
    handleResizeFun = debounce(this.windowResizeHandle, 200)
    bodyClickFun = debounce(this.bodyClickHandle, 200)
    document.getElementsByTagName('html')[0].style.fontSize =
      document.documentElement.clientWidth / 10 + 'px'
    window.addEventListener('resize', handleResizeFun)
    document.addEventListener('click', bodyClickFun)
    this.getData()
  },
  beforeUnmount() {
    window.removeEventListener('resize', handleResizeFun)
    // document.removeEventListener('click', bodyClickFun)
  },
  methods: {
    bodyClickHandle() {
      this.showBillCardDrawer = false
    },
    resizeCard() {
      if (this.$refs.sceneOverallTransactionCard) {
        this.$refs.sceneOverallTransactionCard.resizeHandle()
      }
      if (this.$refs.activitySmallerDimensionCard) {
        this.$refs.activitySmallerDimensionCard.resizeHandle()
      }
      if (this.$refs.activityBiggerDimensionCard) {
        this.$refs.activityBiggerDimensionCard.resizeHandle()
      }
      if (this.$refs.portraitTransactionCard) {
        this.$refs.portraitTransactionCard.resizeHandle()
      }
      if (this.$refs.portraitLabelCard) {
        this.$refs.portraitLabelCard.resizeHandle()
      }
      if (this.$refs.flowMoneyDirectionCard) {
        this.$refs.flowMoneyDirectionCard.resizeHandle()
      }
      if (this.$refs.flowMoneyMonthlyCard) {
        this.$refs.flowMoneyMonthlyCard.resizeHandle()
      }
    },
    windowResizeHandle() {
      document.getElementsByTagName('html')[0].style.fontSize =
        document.documentElement.clientWidth / 10 + 'px'
      this.resizeCard()
    },
    getData() {
      // const personId = "1917110545721368578"
      const personId = "1925841851644944385"
      axios({
        method: 'post',
        url:
          'https://report.aiazs.com/api/open/report/data?personId=' + personId
      }).then((res) => {
        if (res.status === 200 && res.data && res.data.data) {
          this.reportData = res.data.data
          this.riskCodes = this.reportData.billStat.riskCode
          this.processBillStat(this.reportData.billStat.wechatInfo)
          this.processBillStat(this.reportData.billStat.alipayInfo)
          this.getRiskListData().then((res) => {
            if (res.data.data) {
              this.riskData = this.processRiskData(res.data.data)
            }
          })
          this.setChartData(this.reportData)
          this.flowOppositePartyData = {
            counterpartTop7: this.reportData.counterpartTop7,
            counterpartTop15: this.reportData.counterpartTop15,
            counterpartTop30: this.reportData.counterpartTop30,
            counterpartTop90: this.reportData.counterpartTop90,
            counterpartTop180: this.reportData.counterpartTop180,
            counterpartTop360: this.reportData.counterpartTop360
          }
          this.flowBankCardData = this.reportData.card
          this.flowMoneyMonthlyData = this.reportData.monthAvg
        } else { // 请求失败
          ElMessage({
            message: '请求失败',
            grouping: true,
            type: 'error'
          })
        }
      })
    },
    setChartData(data) {
      this.activeSmallerData = {
        hour: data.hour,
        chart_calendar_wechat: data.calculateDailyCountsWechat,
        chart_calendar_alipay: data.calculateDailyCountsAlipay
      }
      this.activeBiggerData = {year: data.month}
      this.portraitTransactionData = data.tagCount
      this.portraitLabelData = data.userTagCount
      this.flowMoneyDirectionData = data.largeCapitalFlow30

      this.lease = data.lease
      this.lending = data.finance || []  // 添加网贷数据
      this.performance = data.payLater || []
      this.records = data.records || []
      this.overallTransactionData = {
        finance: data.finance,
        lease: data.lease,
        payLater: data.payLater,
        recycling: data.recycling
      }
    },
    getRiskListData() {
      return axios({
        method: 'post',
        url: 'https://report.aiazs.com/api/open/risk/list'
      })
    },
    processBillStat(bill) {
      if (bill && bill.num > 0) {
        const arr = []
        for (const it of bill.timeLapseList) {
          const split = it.split(' ')
          arr.push({ account: split[0], timeRange: split[1] })
        }
        bill.timeRanges = arr
      }
    },
    processRiskData(data) {
      for (let i = 0; i < data.length; i++) {
        if (this.riskCodes.includes(data[i].riskCode)) {
          data[i].riskLevel = '风险'
        } else {
          if (data[i].status === 2) data[i].riskLevel = '未命中'
          else data[i].riskLevel = '待实现'
        }
      }
      // 步骤2：进行排序，有风险的排前面，这里假设没有风险的数据riskLevel为其他值（比如'正常'）
      data.sort((a, b) => {
        if (a.riskLevel === '风险' && b.riskLevel !== '风险') {
          return -1
        } else if (a.riskLevel !== '风险' && b.riskLevel === '风险') {
          return 1
        }
        return 0
      })
      return data.filter((item) => item.riskLevel === '风险')
    },
    objectSpanMethod({ rowIndex, columnIndex }) {
      return this.spanArr[rowIndex * this.colFields.length + columnIndex]
    },
    pageAnchorChangeHandle(e) {
      if (e === '#transactionFlowAnalysis') {
        this.showBillCardDrawer = false
      }
    },
    handleCardNameClick(e, cardName) {
      e.stopPropagation();
      this.openBillDrawerWithSearch(cardName)
    },
    hanndleRiskDetail() {
      this.showBillCardDrawer = true
    },
    // 外部唤起抽屉并搜索
    openBillDrawerWithSearch(searchKey = '') {
      this.externalSearchKey = searchKey
      this.showBillCardDrawer = true
      // 清空搜索关键字，避免重复触发
      this.$nextTick(() => {
        this.externalSearchKey = ''
      })
    },
    handleOpenLocation(row) {
      this.openBillDrawerWithSearch(row.counterpart)
    }
  }
}
</script>

<style lang="less">
@import '@/assets/style/main';
</style>
