<template>
  <div class="tab-group" :class="{primary: primary}">
    <div
        class="tab-item"
        :class="{active: currentPos === item.pos}"
        v-for="item in tabArr"
        @click="tabItemClickHandle(item.pos)"
        :key="item.pos"><i class="iconfont" :class="item.icon" v-show="item.icon"/> {{ item.str }}</div>
  </div>
</template>

<script>
export default {
  name: "ChangeTabGroup",
  props: {
    tabData: {
      type: Array,
      default: () => {
        return []
      }
    },
    primary: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    tabData(val) {
      this.setTabArr(val)
    }
  },
  data() {
    return {
      tabArr: [],
      currentPos: 0
    }
  },
  mounted() {
    this.setTabArr(this.tabData)
  },
  methods: {
    setTabArr(val) {
      if (val) {
        const arr = []
        for (let i = 0; i < val.length; i++) {
          if (typeof val[i] === 'string') {
            arr.push({str: val[i], pos: i})
          } else {
            arr.push({str: val[i].str,icon: val[i].icon,pos: i})
          }
        }
        this.tabArr = arr
      } else {
        this.tabArr = []
      }
    },
    tabItemClickHandle(pos) {
      this.currentPos = pos
      this.$emit('change',pos)
    }
  }
}
</script>

<style scoped lang="less">
.tab-group {
    display: flex;
    border-radius: .52083rem;
    border: .00521rem solid #e5e5e5;
    padding: .02083rem .02083rem;
    &.primary {
      border: .00521rem solid #bad3f8;
      background-color: #f2f9ff;
      .tab-item {
        color: #4894e7;
        &.active {
          background-color: #418dff;
          color: #fff;
        }
      }
    }
    .tab-item {
      padding: .02083rem .06083rem;
      font-size: .07292rem;
      i {
        margin-right: 5px;
        font-size: .09292rem;
        &.icon-weixin {
          color: #28c445;
        }
        &.icon-zhifubao {
          color: #52a8f7;
        }
      }
      &:hover {
        cursor: pointer;
      }
      &.active {
        border-radius: .52083rem;
        background-color: #ebf3ff;
        color: #418dff;
      }
    }
}
</style>
