<template>
  <div class="card-box scene-lease-card" v-if="lending.length">
    <div class="card-header">
      <img alt="" src="@/assets/images/arrow-right.png" />
      网贷
      <el-tooltip
        placement="right"
        popper-class="monitor-yt-popover"
        :visible-arrow="false"
      >
        <i class="iconfont icon-question" />
        <template v-slot:content>
          <div
            class="bot__pop"
            style="
              padding: 0;
              width: 2.60417rem;
              line-height: 0.125rem;
              font-size: 0.0625rem;
            "
          >
            统计客户使用网贷的交易记录，可分析出客户的网贷使用情况及还款行为。
          </div>
        </template>
      </el-tooltip>
      <div class="tip">
        <span>{{new Date().getMonth()}}月已还:</span>
        <span class="unit">￥</span>
        <span class="value">{{ monthlyPayment() }}</span>
      </div>
    </div>
    <div class="sub-group">
      <change-tab-group :tab-data="timeTabs" @change="handleFilterDistance" />
      <change-tab-group
        :tab-data="platformTabs"
        @change="handleFilterPlatform"
      />
    </div>
    <div class="data-group">
      <div class="data-item">
        <div class="title">平台数</div>
        <div class="value">
          <div><i class="iconfont icon-system" /></div>
          {{ platformCount() }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">支付次数</div>
        <div class="value">
          <div><i class="iconfont icon-approve" /></div>
          {{ lendingCount() }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">还款金额</div>
        <div class="value">
          <div><i class="iconfont icon-money" /></div>
          {{ totalPaid() }}
        </div>
      </div>
    </div>
    <el-tabs class="scene-tabs" v-model="currentIndex">
      <el-tab-pane v-for="item in tabsData" :key="item.name">
        <template #label>
          <div class="tab-header-box">
            <div class="label">
              {{ item.name.slice(0, 2) }}
              <div v-if="item.tag?.includes('履约中')" class="tag">还款中</div>
            </div>
            <div class="title">{{ item.name }}</div>
          </div>
        </template>
        <div class="sub-box">
          <div class="label">{{ item.name }}</div>
          <div class="info">
            <div class="top">
              <div>{{ item.name }}</div>
              <div v-if="item.tag?.includes('履约中')" class="tag">还款中</div>
            </div>
            <div class="bottom">
              平台<i
                class="iconfont"
                :class="{
                  'icon-weixin': item.type === 'WECHAT',
                  'icon-zhifubao': item.type === 'ALIPAY'
                }"
              />
              <span>{{
                [
                  ...new Set(
                    item.list.map((i) =>
                      i.flowType === 'WECHAT' ? '微信' : '支付宝'
                    )
                  )
                ].join(', ')
              }}</span>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="table-container">
      <el-table
        :data="currentTable"
        :header-cell-style="{
          textAlign: 'center',
          backgroundColor: '#F5F7FA'
        }"
        height="300"
      >
        <el-table-column
          prop="id"
          label="序号"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column
          prop="txTime"
          label="交易时间"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column
          prop="amount"
          label="交易金额"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column
          prop="counterpart"
          label="交易对方"
          align="center"
          show-overflow-tooltip>
          <template #default="scope">
            <el-link type="primary" @click.stop="handleOpenLocation(scope.row)">{{ scope.row.counterpart }}</el-link>
          </template>
        </el-table-column>
        <el-table-column
          prop="txDesc"
          label="交易描述"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column
          prop="txType"
          label="收支类型"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column prop="flowType" label="账单类型" align="center">
          <template #default="scope">
            {{ scope.row.flowType === 'WECHAT' ? '微信' : '支付宝' }}
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination-container">
        <el-pagination
          size="small"
          v-model:current-page="currentPage"
          v-model:page-size="size"
          :page-sizes="[10, 20, 50, 100]"
          :pager-count="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch, onMounted, defineProps, defineEmits } from 'vue'
import ChangeTabGroup from '@/components/ChangeTabGroup'

const props = defineProps({
  lending: {
    type: Array,
    default: () => []
  }
})

// 定义emit
const emit = defineEmits(['openLocation'])
// 响应式状态
const currentIndex = ref('0')
const currentTable = ref([])
const currentPage = ref(1)
const size = ref(10)
const total = ref(0)

const filter = ref({
  flowType: 'all',
  distance: 'all'
})

const tabsData = ref([])

// 监听props.lending变化
watch(
  () => props.lending,
  (newVal) => {
    if (newVal && newVal.length) {
      tabsData.value = [...newVal]
      updateTableData()
    }
  },
  { immediate: true }
)

// 月度应还金额和逾期状态计算
const monthlyPayment = () => {
  const now = new Date()
  const lastMonth = new Date(now.getFullYear(), now.getMonth() - 1, 1)
  const lastMonthEnd = new Date(now.getFullYear(), now.getMonth(), 0)
  
  const total = props.lending.reduce((sum, item) => {
    // 遍历每个借贷项的交易列表
    const monthlyRepayment = item.list?.reduce((itemSum, transaction) => {
      const txDate = new Date(transaction.txTime)
      // 判断是否是上个月的履约还款
      if (
        transaction.txTags.includes('还款') &&
        txDate >= lastMonth &&
        txDate <= lastMonthEnd
      ) {
        return itemSum + (parseFloat(transaction.amount) || 0)
      }
      return itemSum
    }, 0) || 0
    
    return sum + monthlyRepayment
  }, 0)

  return total.toFixed(2)
}


// 计算时间标签
const timeTabs = computed(() => {
  const now = new Date()

  // 计算各时间段的数量
  const getDayCount = (days) => {
    return props.lending.filter((item) => {
      const txDate = new Date(item.beginTime || item.list?.[0]?.txTime)
      const diffTime = now - txDate
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
      return diffDays <= days
    }).length
  }

  const total = props.lending.length
  return [
    `全部(${total})`,
    `7天(${getDayCount(7)})`,
    `15天(${getDayCount(15)})`,
    `1个月(${getDayCount(30)})`,
    `3个月(${getDayCount(90)})`,
    `6个月(${getDayCount(180)})`
  ]
})

// 计算平台标签
const platformTabs = computed(() => {
  const total = props.lending.length
  const platforms = props.lending.reduce(
    (acc, item) => {
      const platform = item.list?.[0]?.flowType || ''
      if (platform === 'WECHAT') acc.wechat++
      else if (platform === 'ALIPAY') acc.alipay++
      return acc
    },
    { wechat: 0, alipay: 0 }
  )

  return [
    `全部(${total})`,
    { str: `微信(${platforms.wechat})`, icon: 'icon-weixin' },
    { str: `支付宝(${platforms.alipay})`, icon: 'icon-zhifubao' }
  ]
})

// 数据统计
const platformCount = () => {
  return new Set(props.lending.map((item) => item.name)).size
}

const lendingCount = () => {
  return props.lending.reduce((sum, item) => {
    if (Array.isArray(item.list)) {
      const subSum = item.list.reduce((subTotal, listItem) => {
        return subTotal + (listItem.txTags.includes('还款') ? 1 : 0)
      }, 0)
      return sum + subSum
    }
    return sum
  }, 0)
}

const totalPaid = () => {
  return props.lending.reduce((sum, item) => {
    if (Array.isArray(item.list)) {
      const subSum = item.list.reduce((subTotal, listItem) => {
        return subTotal + (listItem.txTags.includes('还款') ? listItem.amount : 0)
      }, 0)
      return sum + subSum
    }
    return sum
  }, 0)
}

// 筛选处理函数
const handleFilterDistance = (index) => {
  const days =
    index === 0
      ? 'all'
      : index === 1
      ? 7
      : index === 2
      ? 15
      : index === 3
      ? 30
      : index === 4
      ? 90
      : 180
  filter.value.distance = days
  updateFilteredData()
}

const handleFilterPlatform = (index) => {
  filter.value.flowType =
    index === 0 ? 'all' : index === 1 ? 'WECHAT' : 'ALIPAY'
  updateFilteredData()
}

// 处理分页
const handleSizeChange = (val) => {
  size.value = val
  updateTableData()
}

const handleCurrentChange = (val) => {
  currentPage.value = val
  updateTableData()
}

// 更新表格数据
const updateTableData = () => {
  if (!tabsData.value || !tabsData.value.length) {
    currentTable.value = []
    total.value = 0
    return
  }
  const currentItem = tabsData.value[currentIndex.value]
  if (!currentItem?.list) {
    currentTable.value = []
    total.value = 0
    return
  }
  const allData = currentItem.list
  total.value = allData.length
  const start = (currentPage.value - 1) * size.value
  const end = start + size.value
  currentTable.value = allData.slice(start, end)
}

// 更新筛选后的数据
const updateFilteredData = () => {
  const now = new Date()
  let filteredData = [...props.lending]

  // 筛选时间范围
  if (filter.value.distance !== 'all') {
    filteredData = filteredData.filter((item) => {
      const txDate = new Date(item.beginTime || item.list?.[0]?.txTime)
      const diffTime = now - txDate
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
      return diffDays <= filter.value.distance
    })
  }

  // 筛选平台类型
  if (filter.value.flowType !== 'all') {
    filteredData = filteredData.filter(
      (item) => item.list?.[0]?.flowType === filter.value.flowType
    )
  }
  tabsData.value = filteredData
  currentIndex.value = '0'
  currentPage.value = 1 // 重置页码
  updateTableData()
}

const handleOpenLocation = (row) => {
  emit('openLocation', row)
}


// 监听当前选中的标签页
watch(currentIndex, () => {
  currentPage.value = 1 // 切换tab时重置页码
  updateTableData()
})

// 生命周期钩子
onMounted(() => {
  if (props.lending && props.lending.length) {
    tabsData.value = [...props.lending]
    updateTableData()
  }
})
</script>

<style scoped>
.scene-lease-card {
  position: relative;
}

.table-container {
  display: flex;
  flex-direction: column;
  height: 350px;
}

.pagination-container {
  margin-top: 10px;
  padding: 10px 0;
  display: flex;
  justify-content: flex-end;
  background-color: #fff;
}
</style>
