<template>
  <div class="card-box flow-money-monthly-card" style="margin-bottom: 0.10417rem;">
    <div class="card-header">
      <img alt="" src="@/assets/images/arrow-right.png" />
      月均收支趋势
      <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
        <i class="iconfont icon-question" />
        <template v-slot:content>
          <div class="bot__pop" style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;">
            展示每月的收入与支出趋势，辅助分析年度及月度收支情况。
          </div>
        </template>
      </el-tooltip>
    </div>
    <div class="summary-group">
      <div class="summary-item">
        <div class="summary-title">年总收入</div>
        <div class="summary-value"><span>￥</span>{{ dimensionData.totalIn }}k</div>
      </div>
      <div class="summary-item">
        <div class="summary-title">年总支出</div>
        <div class="summary-value"><span>￥</span>{{ dimensionData.totalOut }}k</div>
      </div>
      <div class="summary-item">
        <div class="summary-title">月均收入</div>
        <div class="summary-value"><span>￥</span>{{ dimensionData.totalInAvg }}k</div>
      </div>
      <div class="summary-item">
        <div class="summary-title">月均支出</div>
        <div class="summary-value"><span>￥</span>{{ dimensionData.totalOutAvg }}k</div>
      </div>
    </div>
    <div class="card-content" style="margin-top: 0.10417rem;">
      <div ref="chartRef" class="chart-container" style="width:100%; height:340px"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: "FlowMoneyMonthlyCard",
  props: {
    dimensionData: {
      type: Object,
      default: () => ({})
    }
  },
  mounted() {
    this.initChart();
  },
  watch: {
    dimensionData: {
      handler() {
        this.initChart();
      },
      deep: true
    }
  },
  methods: {
    resizeHandle() {
      if (this.chartInstance) {
        this.chartInstance.resize();
      }
    },
    initChart() {
      if (!this.dimensionData || !this.dimensionData.data) return;
      const chartDom = document.querySelector('.flow-money-monthly-card .chart-container');
      if (!chartDom) return;
      this.chartInstance = echarts.init(chartDom);

      const { dataList1 = [], dataList2 = [], labelList = [] } = this.dimensionData.data;

      const option = {
       
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
     
        legend: {
          data: ["收入", "支出"],
          right: 20,
          top: 10
        },
        grid: {
          left: 40,
          right: 20,
          bottom: 40,
          top: 60
        },
        xAxis: {
          type: 'category',
          data: labelList
        },
        yAxis: {
          type: 'value',
          name: 'K',
          nameLocation: 'end',
          nameGap: 35,
          minInterval: 1,
          nameTextStyle: {
            align: 'left',
            padding: [0, 10, 0, -20],
            verticalAlign: 'bottom',
            fontWeight: 'bold'
          }
        },
        series: [
          {
            name: '收入',
            type: 'bar',
            data: dataList1,
            itemStyle: { color: '#409EFF' },
            barWidth: 28,
            label: {
              show: true,
              position: 'top',
              distance: 15,
              align: 'center',
              color: '#B0B0B0',
              formatter: function(params) {
                return params.value + 'k';
              }
            }
          },
          {
            name: '支出',
            type: 'bar',
            data: dataList2,
            itemStyle: { color: '#F6B26B' },
            barWidth: 28,
            label: {
              show: true,
              position: 'top',
              distance: 15,
              align: 'center',
              color: '#B0B0B0',
              formatter: function(params) {
                return params.value + 'k';
              }
            }
          }
        ]
      };

      this.chartInstance.setOption(option);
    }
  },
  data() {
    return {
        chart: null
    }
  }
}
</script>

<style scoped>
.flow-money-monthly-card .summary-group {
  display: flex;
  justify-content: space-between;
  margin: 0.2rem 0 0.1rem 0;
}
.summary-item {
  width:1.71875rem;
  border-radius: 8px;
  margin: 0 0.05rem;
  padding: 0.12rem 0.08rem;
  text-align: center;
  background: linear-gradient(180deg,#f9fbff,#f0f5ff);
}
.summary-group .summary-item:nth-child(2n) {
  background: linear-gradient(180deg,#fffcf8,#fff8f0);
}
.summary-title {
    color: #4e5462;
    font-size: .08333rem;
    font-weight: 500;
    width: 100%;
    padding-bottom: 0.03604rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    border-bottom: 0.00521rem solid #e1ecff;
}
.summary-value {
  font-size: 0.10417rem;
  font-weight: bold;
  color: #4e5462;
  margin-top: 0.0604rem;
}
.summary-value span{
    font-size: 0.0625rem;
}
.chart-container {
  width: 100%;
  height: 100%;
  min-height: 340px;
}
</style>
