<template>
    <div class="card-box scene-overall-transaction-card">
      <div class="card-header">
        <img alt="" src="@/assets/images/arrow-right.png"/>
        整体交易
      </div>
      <div class="card-opera-group">
        <change-tab-group :tab-data="dateOptions" @change="handleDateChange"/>
        <change-tab-group :tab-data="paymentOptions" @change="handlePaymentChange"/>
      </div>
      <div class="card-content">
        <div class="chart-container" style="width:100%; height:250px"></div>
      </div>
    </div>
</template>

<script>
import ChangeTabGroup from "@/components/ChangeTabGroup";
import * as echarts from "echarts";

let chartElement = null;

export default {
  name: "SceneOverallTransactionCard",
  components: {ChangeTabGroup},
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      dateOptions: ['全部','7天','1个月','3个月','6个月'],
      paymentOptions: [
        {str: '全部'},
        {str: '微信', icon: 'icon-weixin'},
        {str: '支付宝', icon: 'icon-zhifubao'}
      ],
      selectedDate: '全部',
      selectedPayment: '全部',
      currentData: null,
      categoryMap: {
        finance: '网贷',
        lease: '租赁',
        payLater: '信用履约',
        recycling: '回收'
      }
    }
  },
  watch: {
    dimensionData: {
      handler(val) {
        this.currentData = val;
        this.processDataAndCreateChart();
      },
      deep: true
    }
  },
  mounted() {
    this.currentData = this.dimensionData;
    this.processDataAndCreateChart();
    window.addEventListener('resize', this.resizeHandle);
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose();
    window.removeEventListener('resize', this.resizeHandle);
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize();
    },
    
    handleDateChange(index) {
      this.selectedDate = this.dateOptions[index];
      this.processDataAndCreateChart();
    },
    
    handlePaymentChange(index) {
      this.selectedPayment = this.paymentOptions[index].str;
      this.processDataAndCreateChart();
    },
    
    processDataAndCreateChart() {
      if (!this.currentData) return;
      
      // 处理数据
      const processedData = this.processData(this.currentData);
      this.createChart(processedData);
    },
    
    processData(data) {
      if (!data) return null;
      
      // 自定义类别顺序，将租赁放在第一位
      const categories = ['lease', 'finance', 'payLater', 'recycling'];
      const labelList = categories.map(key => this.categoryMap[key]);
      const dataList1 = []; // 机构数
      const dataList2 = []; // 月均履约金额
      const dataList3 = []; // 周期履约金额
      
      categories.forEach(category => {
        if (data[category]) {
          let filteredData = [...data[category]];
          let daysInPeriod = 0;
          
          // 应用日期筛选
          if (this.selectedDate !== '全部') {
            const now = new Date();
            
            switch (this.selectedDate) {
              case '7天':
                daysInPeriod = 7;
                break;
              case '1个月':
                daysInPeriod = 30;
                break;
              case '3个月':
                daysInPeriod = 90;
                break;
              case '6个月':
                daysInPeriod = 180;
                break;
            }
            
            const cutoffDate = new Date();
            cutoffDate.setDate(now.getDate() - daysInPeriod);
            
            filteredData = filteredData.map(item => {
              const filteredList = item.list.filter(transaction => {
                const txDate = new Date(transaction.txTime);
                return txDate >= cutoffDate;
              });
              return { ...item, list: filteredList };
            });
          } else {
            // 全部数据时，假设为365天
            daysInPeriod = 365;
          }
          
          // 应用支付渠道筛选
          if (this.selectedPayment !== '全部') {
            const paymentType = this.selectedPayment === '微信' ? 'WXPAY' : 'ALIPAY';
            
            filteredData = filteredData.map(item => {
              const filteredList = item.list.filter(transaction => 
                transaction.flowType === paymentType
              );
              return { ...item, list: filteredList };
            });
          }
          
          // 计算数据
          const institutionCount = filteredData.length;
          
          let totalAmount = 0;
          let totalTransactions = 0;
          
          filteredData.forEach(item => {
            item.list.forEach(transaction => {
              // 只统计txTags包含"还款"的交易记录
              if (transaction.txTags && transaction.txTags.includes('还款')) {
                totalAmount += transaction.amount;
                totalTransactions++;
              }
            });
          });
          
          // 月均履约金额: 根据所选周期计算的每月平均值
          let monthlyAverage = 0;
          if (this.selectedDate === '1个月') {
            // 如果选择了1个月，则月均和周期履约金额相同
            monthlyAverage = totalAmount;
          } else {
            // 否则计算月均值
            monthlyAverage = totalTransactions > 0 ? (totalAmount / daysInPeriod) * 30 : 0;
          }
          
          dataList1.push(institutionCount);
          dataList2.push(parseFloat(monthlyAverage.toFixed(2)));
          dataList3.push(parseFloat(totalAmount.toFixed(2)));
        } else {
          dataList1.push(0);
          dataList2.push(0);
          dataList3.push(0);
        }
      });
      
      return {
        labelList,
        dataList1,
        dataList2,
        dataList3
      };
    },
    
    createChart(data) {
      if (!data || !data.labelList) return;
      
      if (chartElement) {
        chartElement.dispose();
      }
      
      chartElement = echarts.init(document.querySelector(".scene-overall-transaction-card .chart-container"));
      chartElement.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
          formatter: function(params) {
            let result = params[0].name + '<br/>';
            params.forEach(item => {
              let colorSpan = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:' + item.color + '"></span>';
              result += colorSpan + item.seriesName + ': ' + item.value + '<br/>';
            });
            return result;
          }
        },
        legend: {
          data: ["机构数", "月均履约金额", "周期履约金额"],
          icon: 'circle',
          orient: 'horizontal',
          textStyle: {
            fontSize: 8
          }
        },
        xAxis: {
          data: data.labelList,
          axisLabel: {
            interval: 0,
            rotate: 0
          }
        },
        yAxis: [
          {
            position: 'left',
            name: '金额',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            position: 'right',
            name: '数量',
            axisLabel: {
              formatter: '{value} 个'
            }
          },
        ],
        grid: {
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [
          {
            name: '机构数',
            type: 'bar',
            smooth: true,
            data: data.dataList1,
            yAxisIndex: 1,
            itemStyle: {
              color: '#5470C6'
            },
            barWidth: '20%'
          },
          {
            name: '月均履约金额',
            type: 'bar',
            smooth: true,
            data: data.dataList2,
            yAxisIndex: 0,
            itemStyle: {
              color: '#91CC75'
            },
            barWidth: '20%'
          },
          {
            name: '周期履约金额',
            type: 'bar',
            smooth: true,
            data: data.dataList3,
            yAxisIndex: 0,
            itemStyle: {
              color: '#FAC858'
            },
            barWidth: '20%'
          },
        ]
      });
    }
  }
}
</script>

<style scoped>
.scene-overall-transaction-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.card-header {
  display: flex;
  align-items: center;
  font-weight: bold;
  margin-bottom: 10px;
}

.card-header img {
  margin-right: 5px;
  width: 16px;
  height: 16px;
}

.card-opera-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.card-content {
  flex: 1;
}
</style>
