<template>
   <div class="flow-money-direction-card card-box">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       大资金流向
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;">
             统计用户资金流向，近一个月金额大于等于400，近三个月金额大于等于800，近半年金额大于等于1000，近一年金额大于等于1200。
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-content">
       <div class="chart-container" style="width:100%; height:300px"></div>
     </div>
   </div>
</template>

<script>
import * as echarts from "echarts";

let chartElement = null
export default {
  name: "FlowMoneyDirectionCard",
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    dimensionData(val) {
      this.createChart(val)
    }
  },
  created() {
  },
  mounted() {
    this.createChart(this.dimensionData)
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose()
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize()
    },
    createChart(data) {
      if (!data || !data.nodes) return;
      if (chartElement) return;
      chartElement = echarts.init(document.querySelector(".flow-money-direction-card .chart-container"));
      chartElement.setOption({
        tooltip: {
          trigger: "item",
          triggerOn: "mousemove",
        },
        series: [
          {
            type: "sankey",
            data: data.nodes,
            links: data.links,
            emphasis: {
              focus: "adjacency",
            },
            levels: [
              {
                depth: 0,
                itemStyle: {
                  color: "#b3cde3",
                },
                lineStyle: {
                  color: "source",
                  opacity: 0.6,
                },
              },
              {
                depth: 1,
                itemStyle: {
                  color: "#fbb4ae",
                },
                lineStyle: {
                  color: "source",
                  opacity: 0.6,
                },
              },
            ],
            lineStyle: {
              curveness: 0.5,
            },
          },
        ],
      });
    }
  }
}
</script>

