<template>
  <div class="tag-group">
    <div class="tag-item" :class="{active: currentPos === index}" v-for="(item,index) in tags" :key="index" @click.stop="itemClickHandle(index)">{{ item }}</div>
  </div>
</template>

<script>
export default {
  name: "TagGroup",
  props: {
    tags: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
       currentPos: 0
    }
  },
  methods: {
    itemClickHandle(pos) {
      this.currentPos = pos
      this.$emit("change",pos)
    }
  }
}
</script>

<style scoped lang="less">
.tag-group {
  display: flex;
  &:not(:first-child) {
    margin-left: 0.06083rem;
  }
  .tag-item {
    min-width: .32292rem;
    font-size: .07292rem;
    border-radius: .52083rem;
    background: #ebf3ff;
    margin: 0 .03125rem;
    height: .125rem;
    font-weight: 100;
    color: #4e5969;
    text-align: center;
    &:hover {
      cursor: pointer;
    }
    &.active {
      border-radius: .52083rem;
      background-color: #418dff;
      color: #fff;
      font-size: .07292rem;
    }
  }
}
</style>
