<template>
  <div class="card-box scene-lease-card">
    <div class="card-header">
      <img alt="" src="@/assets/images/arrow-right.png" />
      租赁
      <el-tooltip
        placement="right"
        popper-class="monitor-yt-popover"
        :visible-arrow="false"
      >
        <i class="iconfont icon-question" />
        <template v-slot:content>
          <div
            class="bot__pop"
            style="
              padding: 0;
              width: 2.60417rem;
              line-height: 0.125rem;
              font-size: 0.0625rem;
            "
          >
            统计客户使用租赁的交易记录，可分析出客户是否是正常的租赁，有无按时交租、存在逾期的行为。
          </div>
        </template>
      </el-tooltip>
      <div class="tip">
        <span>本月应还:</span>
        <span class="unit">￥</span>
        <span class="value">15446~15646</span>
      </div>
    </div>
    <div class="sub-group">
      <change-tab-group
        :tab-data="activeStatusTabs"
        primary
        @change="handleFilterActive"
      />
      <div class="opera-group">
        <change-tab-group :tab-data="timeTabs" @change="handleFilterDistance" />
        <change-tab-group
          :tab-data="platformTabs"
          @change="handleFilterPlatform"
        />
      </div>
    </div>
    <div class="data-group">
      <div class="data-item">
        <div class="title">申请平台数</div>
        <div class="value">
          <div><i class="iconfont icon-system" /></div>
          {{ platformCount }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">申请次数</div>
        <div class="value">
          <div><i class="iconfont icon-edit" /></div>
          {{ totalApplications }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">取消次数</div>
        <div class="value">
          <div><i class="iconfont icon-cancel" /></div>
          {{ cancelledCount }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">通过次数</div>
        <div class="value">
          <div><i class="iconfont icon-approve" /></div>
          {{ approvedCount }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">在租台数</div>
        <div class="value">
          <div><i class="iconfont icon-platform" /></div>
          {{ activeLeaseCount }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">支付押金</div>
        <div class="value">
          <div><i class="iconfont icon-deposit" /></div>
          {{ totalDeposit }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">已支付金额</div>
        <div class="value">
          <div><i class="iconfont icon-money" /></div>
          {{ totalPaid }}
        </div>
      </div>
      <div class="data-item">
        <div class="title">剩余总债权</div>
        <div class="value">
          <div><i class="iconfont icon-balance" /></div>
          {{ remainingBalance }}
        </div>
      </div>
    </div>
    <el-tabs class="scene-tabs" v-model="currentIndex">
      <el-tab-pane v-for="item in tabsData" :key="item.name">
        <template #label>
          <div class="tab-header-box">
            <div class="label" style="overflow: hidden">
              {{ item.name.slice(0, 2) }}
              <div v-if="item.tag.includes('履约中')" class="tag">在租</div>
            </div>
            <div class="title">{{ item.name }}</div>
          </div>
        </template>
        <div class="sub-box">
          <div class="label">{{ item.name }}</div>
          <div class="info">
            <div class="top">
              <div>{{ item.name }}</div>
              <div v-if="item.tag.includes('履约中')" class="tag">在租</div>
            </div>
            <div class="bottom">
              平台<i
                class="iconfont"
                :class="{
                  'icon-weixin': item.type === 2,
                  'icon-zhifubao': item.type === 1
                }"
              />
              <span>{{
                [
                  ...new Set(
                    item.list.map((i) =>
                      i.flowType === 'WECHAT' ? '微信' : '支付宝'
                    )
                  )
                ].join(', ')
              }}</span>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="table-container">
      <el-table
        :header-cell-style="{
          textAlign: 'center',
          backgroundColor: '#F5F7FA'
        }"
        :data="currentTable"
        height="300"
      >
        <el-table-column prop="id" label="序号" align="center" />
        <el-table-column prop="txTime" label="交易时间" align="center" />
        <el-table-column prop="amount" label="消费金额" align="center" show-overflow-tooltip />
        <el-table-column prop="txCategory" label="交易对方" align="center" show-overflow-tooltip>
          <template #default="scope">
            <el-link type="primary" @click.stop="handleOpenLocation(scope.row)">{{ scope.row.counterpart }}</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="txDesc" label="交易描述" align="center" show-overflow-tooltip />
        <el-table-column prop="txType" label="收支类型" align="center" />
        <el-table-column prop="flowType" label="账单类型" align="center" />
      </el-table>
      <div class="pagination-container">
        <el-pagination
          size="small"
          v-model:current-page="currentPage"
          v-model:page-size="size"
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, onMounted, defineProps, computed, defineEmits } from 'vue'
import ChangeTabGroup from '@/components/ChangeTabGroup'

// 定义props
const props = defineProps({
  lease: {
    type: Array,
    default: () => []
  }
})

// 定义emit
const emit = defineEmits(['openLocation'])

// 响应式状态
const currentIndex = ref('0')
const currentTable = ref([])
const currentPage = ref(1)
const size = ref(10)
const total = ref(0)

const filter = ref({
  flowType: 'all',
  active: 'all',
  distance: 'all'
})

const tabsData = ref([...props.lease])

// 计算活跃状态标签
const activeStatusTabs = computed(() => {
  const total = props.lease.length
  const activeCount = props.lease.filter((item) =>
    item.tag.includes('履约中')
  ).length
  return [`全部(${total})`, `在租(${activeCount})`]
})

// 计算时间标签
const timeTabs = computed(() => {
  const total = props.lease.length
  const now = new Date()

  // 计算各时间段的数量
  const getDayCount = (days) => {
    return props.lease.filter((item) => {
      const beginDate = new Date(item.beginTime)
      const diffTime = now - beginDate
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
      return diffDays <= days
    }).length
  }

  return [
    `全部(${total})`,
    `7天(${getDayCount(7)})`,
    `15天(${getDayCount(15)})`,
    `1个月(${getDayCount(30)})`,
    `3个月(${getDayCount(90)})`,
    `6个月(${getDayCount(180)})`
  ]
})

// 计算平台标签
const platformTabs = computed(() => {
  const total = props.lease.length
  const platforms = props.lease.reduce(
    (acc, item) => {
      const platform = item.list?.[0]?.flowType || ''
      if (platform === 'WECHAT') acc.wechat++
      else if (platform === 'ALIPAY') acc.alipay++
      return acc
    },
    { wechat: 0, alipay: 0 }
  )

  return [
    `全部(${total})`,
    { str: `微信(${platforms.wechat})`, icon: 'icon-weixin' },
    { str: `支付宝(${platforms.alipay})`, icon: 'icon-zhifubao' }
  ]
})

// 数据组计算属性
const platformCount = computed(() => {
  // 使用props.lease计算总平台数量，不受筛选影响
  return props.lease.length
})

const totalApplications = computed(() => {
  // 总申请次数就是租赁记录的总数
  return props.lease.length
})

const cancelledCount = computed(() => {
  // 取消的记录数（这里假设没有tag的为取消的记录）
  return props.lease.filter((item) => item.tag.includes('取消')).length
})

const approvedCount = computed(() => {
  // 通过的记录数（有tag的记录）
  return props.lease.filter((item) => !item.tag.includes('被拒')).length
})

const activeLeaseCount = computed(() => {
  // 在租数量（tag包含"履约中"的记录）
  return props.lease.filter((item) => item.tag?.includes('履约中')).length
})

const totalDeposit = computed(() => {
  return props.lease.reduce((sum, item) => sum + (item.amountIn || 0), 0)
})

const totalPaid = computed(() => {
  // 计算已支付总额
  return props.lease.reduce((sum, item) => sum + (item.amount || 0), 0)
})

const remainingBalance = computed(() => {
  // 计算剩余总债权（这里假设是总金额减去已支付金额）
  const totalAmount = props.lease.reduce(
    (sum, item) => sum + (item.amountOut || 0),
    0
  )
  const paidAmount = props.lease.reduce(
    (sum, item) => sum + (item.amountIn || 0),
    0
  )
  return totalAmount - paidAmount
})

// 筛选处理函数
const handleFilterActive = (index) => {
  filter.value.active = index === 0 ? 'all' : 'active'
  updateFilteredData()
}

const handleFilterDistance = (index) => {
  const days =
    index === 0
      ? 'all'
      : index === 1
      ? 7
      : index === 2
      ? 15
      : index === 3
      ? 30
      : index === 4
      ? 90
      : 180
  filter.value.distance = days
  updateFilteredData()
}

const handleFilterPlatform = (index) => {
  filter.value.flowType =
    index === 0 ? 'all' : index === 1 ? 'WECHAT' : 'ALIPAY'
  updateFilteredData()
}

// 处理分页
const handleSizeChange = (val) => {
  size.value = val
  updateTableData()
}

const handleCurrentChange = (val) => {
  currentPage.value = val
  updateTableData()
}

// 更新表格数据
const updateTableData = () => {
  const start = (currentPage.value - 1) * size.value
  const end = start + size.value
  const allData = tabsData.value[currentIndex.value]?.list || []
  total.value = allData.length
  currentTable.value = allData.slice(start, end)
}

// 更新筛选后的数据
const updateFilteredData = () => {
  const now = new Date()
  let filteredData = [...props.lease]

  // 筛选在租状态
  if (filter.value.active !== 'all') {
    filteredData = filteredData.filter((item) => item.tag.includes('履约中'))
  }

  // 筛选时间范围
  if (filter.value.distance !== 'all') {
    filteredData = filteredData.filter((item) => {
      const beginDate = new Date(item.beginTime)
      const diffTime = now - beginDate
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
      return diffDays <= filter.value.distance
    })
  }

  // 筛选平台类型
  if (filter.value.flowType !== 'all') {
    filteredData = filteredData.filter(
      (item) => item.list?.[0]?.flowType === filter.value.flowType
    )
  }
  tabsData.value = filteredData
  currentIndex.value = '0'
  currentPage.value = 1 // 重置页码
  updateTableData()
}

const handleOpenLocation = (row) => {
  emit('openLocation', row)
}

// 生命周期钩子
onMounted(() => {
  updateFilteredData()
})

// 监听器
watch(
  () => currentIndex.value,
  () => {
    currentPage.value = 1 // 切换tab时重置页码
    updateTableData()
  }
)
</script>

<style scoped>
.scene-lease-card {
  position: relative;
}

.table-container {
  display: flex;
  flex-direction: column;
  height: 350px;
}

.pagination-container {
  margin-top: 10px;
  padding: 10px 0;
  display: flex;
  justify-content: flex-end;
  background-color: #fff;
}
</style>
