<template>
  <div class="progress">
    <div class="progress_item"><img src="https://flow-report.oss-cn-chengdu.aliyuncs.com/flow/images/loadding.png"
                                    alt="">
      <div>
        <div>{{percentage}}%</div>
        <div>
          <el-progress
              :stroke-width="15"
              :percentage="percentage"
              :show-text="false"
              striped
              striped-flow
          />
        </div>
        <div v-show="!isError">{{ showText }}</div>
        <div v-show="isError"> {{ textArr[3] }}</div>
        <div v-show="isError">
          <button aria-disabled="false" type="button" class="el-button" style="border: none"><!--v-if--><span class="">重新加载</span></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
let intervalEle = null
export default {
  name: "LoadingPage",
  data() {
    return {
      percentage: 0,
      isError: false,
      textArr:['数据检索中...','数据获取中...','报告生成中...','非常抱歉，由于系统繁忙或网络问题，您的请求暂时未能及时响应，请您稍后再试。'],
      showText: '',
    }
  },
  watch: {
    percentage(val) {
      if (val < 15) this.showText = this.textArr[0]
      else if (val < 60) this.showText = this.textArr[1]
      else this.showText = this.textArr[2]
    }
  },
  mounted() {
    intervalEle =setInterval(this.updatePercentage,100)
  },
  methods: {
    updatePercentage() {
      this.percentage += 35
      if (this.percentage >= 100) {
        if (intervalEle) clearInterval(intervalEle)
        this.$emit('loadFinish')
      }
    }
  },
  unmounted() {
    if (intervalEle) clearInterval(intervalEle)
  }
}
</script>

<style scoped lang="less">
@media screen and (max-width: 768px) {
  .progress {
    width: 100%;
    padding-top: 30vh;
    display: flex;
    align-items: center;
    justify-content: center
  }

  .progress .progress_item {
    width: 100%;
    position: relative
  }

  .progress .progress_item > img {
    width: 100%;
    object-fit: contain
  }

  .progress .progress_item > div {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0
  }

  .progress .progress_item > div > :first-child {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%);
    margin: auto;
    padding-top: 4vw;
    font-size: 8.8vw;
    background: linear-gradient(180deg, #2a71ff, #6196ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: 700;
    text-align: center
  }

  .progress .progress_item > div > :nth-child(2) {
    margin: auto;
    width: 72%;
    padding-top: 17.9vw
  }

  .progress .progress_item > div > :nth-child(3) {
    height: 100%;
    text-align: center;
    padding-top: 10vw;
    color: #4684ff;
    font-size: 4.5vw
  }

  .progress .progress_item > div > :nth-child(4) {
    padding-top: 10vw;
    width: 100%;
    display: flex;
    justify-content: center
  }

  .progress .progress_item > div > :nth-child(4) button {
    font-size: .09375rem;
    width: .71354rem;
    height: .19792rem;
    color: #fff;
    border-radius: .07813rem;
    background: #3270ff;
  }
}

@media screen and (min-width: 768px) {
  .progress {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center
  }

  .progress .progress_item {
    width: 63%;
    height: 63%;
    position: relative
  }

  .progress .progress_item > img {
    width: 100%;
    object-fit: contain
  }

  .progress .progress_item > div {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0
  }

  .progress .progress_item > div > :first-child {
    margin: auto;
    padding-top: .4vw;
    font-size: 3.8vw;
    background: linear-gradient(180deg, #2a71ff, #6196ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: 700;
    text-align: center
  }

  .progress .progress_item > div > :nth-child(2) {
    margin: auto;
    width: 72%;
    padding-top: .9vw
  }

  .progress .progress_item > div > :nth-child(3) {
    height: 100%;
    text-align: center;
    padding-top: 5.5vw;
    color: #4684ff;
    font-size: 1.4vw
  }

  .progress .progress_item > div > :nth-child(4) {
    padding-top: 6vw;
    width: 100%;
    display: flex;
    justify-content: center
  }

  .progress .progress_item > div > :nth-child(4) button {
    font-size: .15625rem;
    width: 1.14063rem;
    height: .33854rem;
    color: #fff;
    border-radius: .07813rem;
    background: #3270ff
  }
}

:deep(.el-progress-bar__inner.el-progress-bar__inner--striped.el-progress-bar__inner--striped-flow) {
  animation-duration: .1s !important
}

:deep(.el-progress-bar__inner--striped.el-progress-bar__inner--striped-flow) {
  animation: striped-flow .1s linear infinite !important
}
</style>
