<template>
   <div class="card-box" style="margin-bottom: 0.10417rem;">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       交易对方-列表TOP
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;">
             统计客户在指定时段的交易数据，可分析出客户有哪些交易行为、区间金额及消费水平。
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-opera-group">
       <change-tab-group :tab-data="['频次TOP','金额TOP']" primary @change="handleTabChange"/>
     </div>
     <div class="sub-group">
       <tag-group :tags="['7天','15天','1个月','3个月','半年','1年']" @change="handleTimeRangeSelect"/>
     </div>
     <div class="card-content" style="margin-top: 0.10417rem;">
       <el-table
           ref="tableRef"
           :data="tableData"
           height="420"
           :header-cell-style="{
                    textAlign: 'center',
                    backgroundColor: '#F5F7FA',
                  }"
           :cell-style="{ textAlign: 'center' }"
           :default-sort="defaultSort"
       >
         <el-table-column
             type="index"
             label="序号"
             width="60"
         />
         <el-table-column
             prop="counterpart"
             label="交易对象"
         />
         <el-table-column
             prop="count"
             label="总次数"
             sortable
         />
         <el-table-column
             prop="totalAmount"
             label="总额"
             sortable
         />
         <el-table-column
             prop="totalOut"
             label="总支出"
         />
         <el-table-column
             prop="totalIn"
             label="总收入"
         />
         <el-table-column
             prop="totalOther"
             label="其他"
         />
         <el-table-column
             prop="totalBalance"
             label="收支差额"
         />
         <el-table-column
             label="操作"
         >
           <template #default="scope">
             <!-- 这里可以放操作按钮 -->
             <el-button type="text" size="small" @click.stop="handleOpenLocation(scope.row)">详情</el-button>
           </template>
         </el-table-column>
       </el-table>
     </div>
   </div>
</template>

<script>
import ChangeTabGroup from "@/components/ChangeTabGroup";
import TagGroup from "@/components/TagGroup";
export default {
  name: "FlowOppositePartyCard",
  components: {TagGroup, ChangeTabGroup},
  emits: ['openLocation'],
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      currentTimeRange: '7天',
      currentTab: 0, // 0: 频次TOP, 1: 余额TOP
    }
  },
  computed: {
    tableData() {
      // 根据不同的时间范围返回对应的数据
      const timeRangeMap = {
        '7天': 'counterpartTop7',
        '15天': 'counterpartTop15',
        '1个月': 'counterpartTop30',
        '3个月': 'counterpartTop90',
        '半年': 'counterpartTop180',
        '1年': 'counterpartTop360'
      }
      return this.data[timeRangeMap[this.currentTimeRange]] || [];
    },
    defaultSort() {
      // 频次TOP：按count降序，余额TOP：按totalAmount降序
      if (this.currentTab === 0) {
        return { prop: 'count', order: 'descending' }
      } else if (this.currentTab === 1) {
        return { prop: 'totalAmount', order: 'descending' }
      }
      return {};
    }
  },
  methods: {
    handleTimeRangeSelect(index) {
      const timeRanges = ['7天','15天','1个月','3个月','半年','1年'];
      this.currentTimeRange = timeRanges[index];
      console.log(this.currentTimeRange)
    },
    handleTabChange(index) {
      this.currentTab = index;
      // 频次TOP：按count降序，金额TOP：按totalAmount降序
      if (index === 0) {
        this.$refs.tableRef.sort('count', 'descending');
      } else if (index === 1) {
        this.$refs.tableRef.sort('totalAmount', 'descending');
      }
    },
    handleOpenLocation(row) {
      this.$emit('openLocation', row)
    }
  }
}
</script>

<style scoped>

</style>
