<template>
   <div class="activity-bigger-card card-box">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       年活跃数
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;color: #ff7d3d;">
             对客户每月中各天数的活动频次进行统计分析，可分析得出用户的工作情况、作息规律、交易频次。【不包含自动收支、自动退款流水】
           </div>
         </template>
       </el-tooltip>
       <span class="remark">注：活跃度不包含不计收支流水数据统计。</span>
     </div>
     <div class="card-content">
       <div class="chart-container" style="width:100%; height:300px"></div>
     </div>
   </div>
</template>

<script>
import * as echarts from "echarts";

let chartElement = null
export default {
  name: "ActivityBiggerDimensionCard",
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    dimensionData(val) {
      this.createChart(val)
    }
  },
  created() {
  },
  mounted() {
    this.createChart(this.dimensionData)
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose()
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize()
    },
    createChart(obj) {
      if (!obj || !obj.year) return;
      const data = obj.year
      if (!data || !data.labelList) return
      if (chartElement) return;
      chartElement = echarts.init(document.querySelector(".activity-bigger-card .chart-container"));
      chartElement.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          data: ["微信", "支付宝"],
          icon: 'circle',
          right: '5%',
          top: '5%',
          orient: 'horizontal',
          textStyle: {
            fontSize: 8   // 字体大小
          }
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: data.labelList
          }
        ],
        yAxis: {},
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        series: [
          {
            name: '微信',
            type: 'bar',
            smooth: true,
            data: data.dataList1,
          },
          {
            name: '支付宝',
            type: 'bar',
            smooth: true,
            data: data.dataList2,
          },
        ]
      });
    }
  }
}
</script>

