<template>
   <div class="bill-card-search-header content-box">
     <div class="search">
       <div class="search-type">
         <el-select v-model="queryParams.searchType" size="small" placeholder="请选择">
           <template #prefix>
             <span>搜索条件:</span>
           </template>
           <el-option
              v-for="item in ['全部','交易对方和交易描述','交易对方','交易描述','收支类型','收/付款方式']"
              :key="item"
              :value="item"
              :label="item"
           />
         </el-select>
       </div>
       <el-input v-model="queryParams.searchKey" placeholder="请输入内容" class="keyword" @keyup.enter="handleQuery"/>
       <div class="blue" @click="handleQuery">搜索</div>
       <div class="blue reset" @click="resetQuery">重置</div>
       <div class="blue toggle" @click="toggleMore = !toggleMore">
         {{ toggleMore ? '收起' : '展开' }}
       </div>
     </div>

     <!-- 常用关键字 -->
     <div class="quick-keywords">
       常用关键字：
       <el-link @click="searchShow('租')">租</el-link>
       |
       <el-link @click="searchShow('贷')">贷</el-link>
       |
       <el-link @click="searchShow('金融')">金融</el-link>
       |
       <el-link @click="searchShow('回收')">回收</el-link>
       |
       <el-link @click="searchShow('还款')">还款</el-link>
       |
       <el-link @click="searchShow('扣款')">扣款</el-link>
       |
       <el-link @click="searchShow('分期')">分期</el-link>
       |
       <el-link @click="searchShow('投资')">投资</el-link>
       |
       <el-link @click="searchShow('押金')">押金</el-link>
       |
       <el-link @click="searchShow('冻结')">冻结</el-link>
       |
       <el-link @click="searchShow('攒钱自动转入')">攒钱自动转入</el-link>
       |
       <el-link @click="searchShow('零钱通转出')">零钱通转出</el-link>
       |
       <el-link @click="searchShow('转入零钱通')">转入零钱通</el-link>
     </div>

     <!-- 高级搜索条件 -->
     <div class="more" v-if="toggleMore">
       <div class="search-row">
         账单来源：
         <el-radio-group v-model="queryParams.flowType">
           <el-radio-button label="全部">全部</el-radio-button>
           <el-radio-button label="WECHAT">微信</el-radio-button>
           <el-radio-button label="ALIPAY">支付宝</el-radio-button>
         </el-radio-group>
         &nbsp;收支类型：
         <el-radio-group v-model="queryParams.txType">
           <el-radio-button label="全部">全部</el-radio-button>
           <el-radio-button label="收入">收入</el-radio-button>
           <el-radio-button label="支出">支出</el-radio-button>
           <el-radio-button label="其他">其他</el-radio-button>
         </el-radio-group>
       </div>
       <div class="search-row">
         时间范围：
         <el-button-group>
           <el-button @click="chooseDate(1)">近1个月</el-button>
           <el-button @click="chooseDate(2)">近2个月</el-button>
           <el-button @click="chooseDate(3)">近3个月</el-button>
           <el-button @click="chooseDate(12)">近1年</el-button>
         </el-button-group>
         &nbsp;
         <el-date-picker
           v-model="queryParams.dateRange"
           type="daterange"
           format="YYYY-MM-DD"
           value-format="YYYY-MM-DD"
           range-separator="至"
           start-placeholder="开始日期"
           end-placeholder="结束日期"
           style="max-width: 300px;"
         ></el-date-picker>
       </div>
       <div class="search-row">
         金额范围：
         <el-button-group>
           <el-button @click="setAmountRange(0, 500)">0 - 500</el-button>
           <el-button @click="setAmountRange(500, 1000)">500 - 1000</el-button>
           <el-button @click="setAmountRange(1000, 2000)">1000 - 2000</el-button>
           <el-button @click="setAmountRange(2000, 3500)">2000 - 3500</el-button>
           <el-button @click="setAmountRange(3500, null)">3500+</el-button>
         </el-button-group>
         &nbsp;
         <el-input v-model="queryParams.amountRange1" placeholder="金额起点" clearable
                   @keyup.enter="handleQuery" style="width:110px;"/> -
         <el-input v-model="queryParams.amountRange2" placeholder="金额终点" clearable
                   @keyup.enter="handleQuery" style="width:110px;"/>
       </div>
     </div>
   </div>
</template>

<script>
export default {
  name: "BillCardSearchHeader",
  data() {
    return {
      toggleMore: false,
      queryParams: {
        searchType: '全部',
        searchKey: '',
        flowType: '全部',
        txType: '全部',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      }
    }
  },
  methods: {
    handleQuery() {
      this.$emit('search', this.queryParams);
    },
    resetQuery() {
      this.queryParams = {
        searchType: '全部',
        searchKey: '',
        flowType: '全部',
        txType: '全部',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      };
      this.$emit('search', this.queryParams);
    },
    searchShow(keyword) {
      this.queryParams.searchKey = keyword;
      this.handleQuery();
    },
    chooseDate(months) {
      const endDate = new Date();
      const startDate = new Date();
      startDate.setMonth(startDate.getMonth() - months);
      
      this.queryParams.dateRange = [
        startDate.toISOString().split('T')[0],
        endDate.toISOString().split('T')[0]
      ];
    },
    setAmountRange(min, max) {
      this.queryParams.amountRange1 = min;
      this.queryParams.amountRange2 = max || '';
    },
    updateSearchParams(params) {
      // 外部更新搜索参数
      this.queryParams = { ...params };
    }
  }
}
</script>

<style scoped lang="less">
.content-box {
  font-size: .07813rem;
  .search {
    height: .26042rem;
    display: flex;
    align-items: center;
    margin-bottom: .05208rem;
    position: relative;
    .search-type {
      width: 1.04167rem;
      margin-right: .0625rem;
      color: #a8abb2;
    }
    .el-select,.el-select__wrapper.el-tooltip__trigger.el-tooltip__trigger{
      border: none !important;
    }
    :deep(.el-select__wrapper) {
      align-items: center;
      background-color: var(--el-fill-color-blank);
      border-radius: var(--el-border-radius-base);
      box-shadow: 0 0 0 .00521rem var(--el-border-color) inset;
      box-sizing: border-box;
      cursor: pointer;
      display: flex;
      font-size: .07292rem;
      gap: .03125rem;
      line-height: .125rem;
      min-height: .16667rem;
      padding: .02083rem .0625rem;
      position: relative;
      text-align: left;
      transition: var(--el-transition-duration);
    }
    .keyword {
      width: 1.04167rem;
      height: .16667rem;
      border: none;
      background: #f7f7f7;
      border-radius: .02604rem;
      color: #b6b6b6;
    }
    .blue {
      display: flex;
      height: .16667rem;
      padding: 0 .08333rem;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: .07292rem;
      font-weight: 400;
      border-radius: .02083rem;
      background: #418dff;
      margin: 0 .0625rem;
      cursor: pointer;
    }
    .reset {
      color: #333;
      background: #fff;
      border: .00521rem solid #ddd;
      margin-left: 0;
    }
    .toggle {
      margin-left: 0;
    }
  }

  .quick-keywords {
    margin-bottom: .05208rem;
    font-size: .07292rem;
    color: #666;
    
    .el-link {
      margin: 0 .02083rem;
      font-size: .07292rem;
    }
  }

  .more {
    background: #f9f9f9;
    padding: .05208rem;
    border-radius: .02604rem;
    
    .search-row {
      display: flex;
      align-items: center;
      margin-bottom: .05208rem;
      font-size: .07292rem;
      
      &:last-child {
        margin-bottom: 0;
      }
      
      .el-radio-group {
        margin-right: .0625rem;
      }
      
      .el-button-group {
        margin-right: .0625rem;
        
        .el-button {
          font-size: .07292rem;
          padding: .02083rem .04167rem;
        }
      }
      
      .el-date-picker {
        width: 1.5rem;
      }
      
      .el-input {
        font-size: .07292rem;
      }
    }
  }
}
</style>
