<template>
   <div class="portrait_label_card card-box">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       画像标签
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;">
             细分画像场景，可分析出客户画像模型、解析用户消费潜力。
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-opera-group">
        <change-tab-group :tab-data="['频次','金额']" primary/>
     </div>
     <div class="card-content">
       <div class="chart-container" style="width:100%; height:300px"></div>
     </div>
   </div>
</template>

<script>
import ChangeTabGroup from "@/components/ChangeTabGroup";
import * as echarts from "echarts";

let chartElement = null;
export default {
  name: "PortraitLabelCard",
  components: {ChangeTabGroup},
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    dimensionData(val) {
      this.createChart(val)
    }
  },
  created() {
  },
  mounted() {
    this.createChart(this.dimensionData)
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose()
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize()
    },
    createChart(data) {
      if (!data || !data.labelList) return
      if (chartElement) return;
      const result = data.labelList.map((label, index) => ({
        name: label,
        value: data.dataList1[index],
      }));
      chartElement = echarts.init(document.querySelector(".portrait_label_card .chart-container"));
      chartElement.setOption({
        tooltip: {
          trigger: "item",
        },
        legend: {
          type: "scroll",
          right: 0,
          top: 20,
          bottom: 20,
          orient: "vertical",
        },
        series: [
          {
            name: "用户画像",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 40,
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: result,
          },
        ],
      });
    }
  }
}
</script>

<style scoped>

</style>
