<template>
   <div class="portrait_transaction_card card-box">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       交易对方标签
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;;">
             敏感关键词匹配，可分析客户异常标签命中的交易使用场景等。
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-content">
       <div class="chart-container" style="width:100%; height:300px"></div>
     </div>
   </div>
</template>

<script>
import * as echarts from "echarts";

let chartElement = null
export default {
  name: "PortraitTransactionCard",
  props: {
    dimensionData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    dimensionData(val) {
      this.createChart(val)
    }
  },
  created() {
  },
  mounted() {
    this.createChart(this.dimensionData)
  },
  beforeUnmount() {
    if (chartElement) chartElement.dispose()
  },
  methods: {
    resizeHandle() {
      if (chartElement) chartElement.resize()
    },
    createChart(data) {
      if (!data || !data.labelList) return
      if (chartElement) return;
      const result = data.labelList.map((label, index) => ({
        name: label,
        value: data.dataList1[index],
      }));
      chartElement = echarts.init(document.querySelector(".portrait_transaction_card .chart-container"));
      chartElement.setOption({
        series: [
          {
            name: "交易标签",
            type: "pie",
            radius: "50%",
            itemStyle: {
              borderRadius: 8,
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 40,
                fontWeight: "bold",
              },
            },
            data: result,
          },
        ],
      });
    }
  }
}
</script>

