<template>
  <div class="card-box bill-card">
    <bill-card-search-header @search="handleSearch" ref="billCardSearchHeader" />
    <div class="table-box">
      <el-table
        :data="paginatedData"
        :header-cell-style="{
          textAlign: 'center',
          backgroundColor: '#F5F7FA'
        }"
        height="460"
      >
        <el-table-column
          type="index"
          label="序号"
          width="60"
          show-overflow-tooltip
        >
          <template #default="scope">
            {{ (currentPage - 1) * pageSize + scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column
          prop="flowType"
          label="平台"
          width="80"
          show-overflow-tooltip
        >
          <template #default="scope">
            {{
              scope.row.flowType === 'WECHAT'
                ? '微信'
                : scope.row.flowType === 'ALIPAY'
                ? '支付宝'
                : scope.row.flowType
            }}
          </template>
        </el-table-column>
        <el-table-column
          prop="txType"
          label="收/支/其他"
          width="100"
          show-overflow-tooltip
        >
          <template #default="scope">
            <el-link @click="searchKeyword(scope.row.txType)" :underline="false" type="primary">
              {{ scope.row.txType }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column
          prop="counterpart"
          label="交易对方"
          width="150"
          show-overflow-tooltip
        >
          <template #default="scope">
            <el-link @click="searchKeyword(scope.row.counterpart)" :underline="false" type="primary">
              {{ scope.row.counterpart }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column
          prop="payType"
          label="收/付款方式"
          width="150"
          show-overflow-tooltip
        >
          <template #default="scope">
            <el-link @click="searchKeyword(scope.row.payType)" :underline="false" type="primary">
              {{ scope.row.payType }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column
          prop="amount"
          label="金额"
          width="100"
          sortable
          show-overflow-tooltip
        >
          <template #default="scope"> ¥{{ scope.row.amount }} </template>
        </el-table-column>
        <el-table-column
          prop="txTime"
          label="时间"
          width="150"
          show-overflow-tooltip
        />
        <el-table-column
          prop="txDesc"
          label="交易描述"
          min-width="150"
          show-overflow-tooltip
        >
          <template #default="scope">
            <el-link @click="searchKeyword(scope.row.txDesc)" :underline="false" type="primary">
              {{ scope.row.txDesc }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column
          label="假日"
          width="100"
          fixed="right"
          show-overflow-tooltip
        >
          <template #default="scope">
            {{ scope.row.holiday == 2 ? '是' : '-' }}
          </template>
        </el-table-column>
        <el-table-column
          label="工作日"
          width="100"
          fixed="right"
          show-overflow-tooltip
        >
          <template #default="scope">
            {{ scope.row.workingHours == 2 ? '是' : '-' }}
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="table-bottom">
      <div class="summary">
        <div>【单位K:千】</div>
        <div>总收入：{{ summary.totalIncome }}K</div>
        <div>总支出：{{ summary.totalExpense }}K</div>
        <div>其他：{{ summary.totalOther }}K</div>
        <div>共：{{ summary.totalCount }} 笔</div>
        <div>差额：{{ summary.balance }}K</div>
      </div>
      
      <!-- 分页组件 -->
      <div class="pagination-box">
        <el-pagination
          size="small"
          background
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[10, 20, 50, 100]"
          :total="filteredData.length"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import BillCardSearchHeader from '@/components/BillCardSearchHeader'
export default {
  name: 'BillCard',
  components: { BillCardSearchHeader },
  props: {
    billData: {
      type: Array,
      default: () => []
    },
    externalSearchKey: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      searchParams: {},
      currentPage: 1,
      pageSize: 10
    }
  },
  computed: {
    filteredData() {
      let data = this.billData

      if (!this.searchParams || Object.keys(this.searchParams).length === 0) {
        return data
      }

      // 关键字搜索
      if (this.searchParams.searchKey) {
        const keyword = this.searchParams.searchKey.toLowerCase()
        data = data.filter((item) => {
          if (this.searchParams.searchType === '全部') {
            return (
              item.counterpart?.toLowerCase().includes(keyword) ||
              item.txDesc?.toLowerCase().includes(keyword) ||
              item.txType?.toLowerCase().includes(keyword) ||
              item.payType?.toLowerCase().includes(keyword)
            )
          } else if (this.searchParams.searchType === '交易对方') {
            return item.counterpart?.toLowerCase().includes(keyword)
          } else if (this.searchParams.searchType === '交易描述') {
            return item.txDesc?.toLowerCase().includes(keyword)
          } else if (this.searchParams.searchType === '交易对方和交易描述') {
            return (
              item.counterpart?.toLowerCase().includes(keyword) ||
              item.txDesc?.toLowerCase().includes(keyword)
            )
          } else if (this.searchParams.searchType === '收支类型') {
            return item.txType?.toLowerCase().includes(keyword)
          } else if (this.searchParams.searchType === '收/付款方式') {
            return item.payType?.toLowerCase().includes(keyword)
          }
          return true
        })
      }

      // 账单来源筛选
      if (this.searchParams.flowType && this.searchParams.flowType !== '全部') {
        data = data.filter(
          (item) => item.flowType === this.searchParams.flowType
        )
      }

      // 收支类型筛选
      if (this.searchParams.txType && this.searchParams.txType !== '全部') {
        data = data.filter((item) => item.txType === this.searchParams.txType)
      }

      // 时间范围筛选
      if (
        this.searchParams.dateRange &&
        this.searchParams.dateRange.length === 2
      ) {
        const [startDate, endDate] = this.searchParams.dateRange
        data = data.filter((item) => {
          const txDate = item.txTime.split(' ')[0]
          return txDate >= startDate && txDate <= endDate
        })
      }

      // 金额范围筛选
      if (
        this.searchParams.amountRange1 !== '' ||
        this.searchParams.amountRange2 !== ''
      ) {
        data = data.filter((item) => {
          const amount = parseFloat(item.amount)
          const min =
            this.searchParams.amountRange1 !== ''
              ? parseFloat(this.searchParams.amountRange1)
              : 0
          const max =
            this.searchParams.amountRange2 !== ''
              ? parseFloat(this.searchParams.amountRange2)
              : Infinity
          return amount >= min && amount <= max
        })
      }

      return data
    },
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredData.slice(start, end)
    },
    summary() {
      const data = this.filteredData
      let totalIncome = 0
      let totalExpense = 0
      let totalOther = 0

      data.forEach((item) => {
        const amount = parseFloat(item.amount)
        if (item.txType === '收入') {
          totalIncome += amount
        } else if (item.txType === '支出') {
          totalExpense += amount
        } else {
          totalOther += amount
        }
      })

      return {
        totalIncome: (totalIncome / 1000).toFixed(1),
        totalExpense: (totalExpense / 1000).toFixed(1),
        totalOther: (totalOther / 1000).toFixed(1),
        totalCount: data.length,
        balance: ((totalIncome - totalExpense) / 1000).toFixed(1)
      }
    }
  },
  mounted() {
    // 如果有外部搜索关键字，自动执行搜索
    if (this.externalSearchKey) {
      this.handleExternalSearch()
    }
  },
  watch: {
    externalSearchKey(newVal) {
      if (newVal) {
        this.handleExternalSearch()
      }
    }
  },
  methods: {
    handleSearch(params) {
      this.searchParams = { ...params }
      this.currentPage = 1 // 搜索时重置到第一页
      this.$emit('search', params)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.currentPage = 1 // 改变页面大小时重置到第一页
    },
    handleCurrentChange(val) {
      this.currentPage = val
    },
    handleExternalSearch() {
      // 设置搜索参数
      this.searchParams = {
        searchType: '全部',
        searchKey: this.externalSearchKey,
        flowType: '全部',
        txType: '全部',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      }
      this.currentPage = 1
      // 通知搜索头部组件更新
      this.$refs.billCardSearchHeader?.updateSearchParams(this.searchParams)
      this.$emit('search', this.searchParams)
    },
    searchKeyword(keyword) {
      this.searchParams = {
        searchType: '全部',
        searchKey: keyword,
        flowType: '全部',
        txType: '全部',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      }
      this.currentPage = 1
      // 通知搜索头部组件更新
      this.$refs.billCardSearchHeader?.updateSearchParams(this.searchParams)
      this.$emit('search', this.searchParams)
    }
  }
}
</script>

<style scoped lang="less">
.table-box {
  margin-bottom: 0.10417rem;
  position: relative;
}

.pagination-box {
  display: flex;
  justify-content: center;
  margin: 0.10417rem 0;

  :deep(.el-pagination) {
    .el-pagination__total,
    .el-pagination__sizes,
    .el-pagination__jump {
      font-size: 0.07292rem;
    }

    .el-pager li {
      font-size: 0.07292rem;
      min-width: 0.16667rem;
      height: 0.16667rem;
      line-height: 0.16667rem;
    }

    .btn-prev,
    .btn-next {
      font-size: 0.07292rem;
      min-width: 0.16667rem;
      height: 0.16667rem;
      line-height: 0.16667rem;
    }
  }
}

.table-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.10417rem 0;
  
  .summary {
    display: flex;
    color: #818799;
    :not(:first-child) {
      margin-left: 0.10417rem;
    }
  }
  
  .pagination-box {
    margin: 0;
    
    :deep(.el-pagination) {
      .el-pagination__total,
      .el-pagination__sizes,
      .el-pagination__jump {
        font-size: 0.07292rem;
      }

      .el-pager li {
        font-size: 0.07292rem;
        min-width: 0.16667rem;
        height: 0.16667rem;
        line-height: 0.16667rem;
      }

      .btn-prev,
      .btn-next {
        font-size: 0.07292rem;
        min-width: 0.16667rem;
        height: 0.16667rem;
        line-height: 0.16667rem;
      }
    }
  }
}
</style>
