<template>
  <div class="card-box">
    <div class="card-header">
      <img alt="" src="@/assets/images/arrow-right.png" />
      银行卡列表
      <el-tooltip
        placement="right"
        popper-class="monitor-yt-popover"
        :visible-arrow="false"
      >
        <i class="iconfont icon-question" />
        <template v-slot:content>
          <div
            class="bot__pop"
            style="
              padding: 0;
              width: 2.60417rem;
              line-height: 0.125rem;
              font-size: 0.0625rem;
            "
          >
            统计客户常用银行卡、信用卡，可分析得出客户主要收入支出卡和信用卡使用情况。
          </div>
        </template>
      </el-tooltip>
    </div>
    <div class="sub-group">
      <div class="card-info">
        <div>储蓄卡:</div>
        <span>{{ dimensionData.numDepositCard }}张;</span><span>信用卡:</span
        ><span>{{ dimensionData.numCreditCard }}张</span>
      </div>
    </div>
    <div class="card-content" style="margin-top: 0.10417rem">
      <el-table :data="dimensionData.list">
        <el-table-column
          prop="name"
          label="银行卡名称"
          header-align="left"
          align="left"
        >
          <template v-slot="scope">
            <span
              style="color: #409EFF; cursor: pointer"
              @click="(e) => $emit('card-name-click', e, scope.row.name || (scope.row.payType ? scope.row.payType.split('(')[0] : ''))"
            >
              {{ scope.row.name || (scope.row.payType ? scope.row.payType.split('(')[0] : '') }}
            </span>
          </template>
        </el-table-column>

        <el-table-column
          prop="lastFourDigits"
          label="银行卡尾号"
          header-align="left"
          align="left"
          :width="180"
        >
          <template v-slot="scope">
            {{ scope.row.lastFourDigits || (scope.row.payType ? scope.row.payType.match(/\((\d+)\)/)?.[1] : '') }}
            <img
              v-if="
                scope.row.platform && scope.row.platform.indexOf('WECHAT') > -1
              "
              src="@/assets/images/wx.png"
              style="width: 20px; vertical-align: middle; margin-left: 4px"
              alt=""
            />
            <img
              v-if="
                scope.row.platform && scope.row.platform.indexOf('ALIPAY') > -1
              "
              src="@/assets/images/zfb.png"
              style="width: 20px; vertical-align: middle; margin-left: 4px"
              alt=""
            />
            <el-tag
              v-if="scope.row.defaultCard"
              type="danger"
              style="margin-left: 4px"
              >常用卡</el-tag
            >
          </template>
        </el-table-column>
        <el-table-column
          label="交易次数"
         
          header-align="left"
          align="left"
        >
          <template v-slot="scope">
            <div class="progress-bar-bg">
              <div
                class="progress-bar-fg"
                :style="{ width: scope.row.ratio + '%' }"
              ></div>
              <span class="progress-bar-text">
                {{ scope.row.count }} / {{ scope.row.ratio }}%
              </span>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="totalIn"
          label="提现到卡"
          header-align="left"
          align="left"
        />
        <el-table-column
          prop="totalOut"
          label="从卡支出"
          header-align="left"
          align="left"
        />
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "FlowBankCardCard",
  props: {
    dimensionData: {
      type: Object,
      required: true,
    },
  },
  methods: {
    formatCountRatio(row) {
      // ratio 可能是 0.5881 这种小数
      const percent = row.ratio + "%";
      return `${row.count} / ${percent}`;
    },
  },
};
</script>

<style scoped lang="less">
.card-info {
  display: flex;
  color: #4e5969;
  font-size: 0.07292rem;
  font-weight: 400;
  span {
    margin-left: 0.06417rem;
  }
}
.el-table .cell {
  text-align: left !important;
}
.progress-bar-bg {
  position: relative;
  background: rgba(139, 228, 182, 0.3); // 浅绿
  border-radius: 10px;
  height: 20px;
  width: 100%;
  overflow: hidden;
  .progress-bar-fg {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background: rgba(139, 228, 182, 0.6); // 深绿
    border-radius: 10px 0 0 10px;
    transition: width 0.3s;
    z-index: 1;
  }
  .progress-bar-text {
    position: relative;
    z-index: 2;
    line-height: 20px;
    padding-left: 8px;
  }
}
</style>
